  margin: auto;
  overflow: hidden;
  position: relative;
}
.wd-slider__slides {
  width: 300%;
  display: flex;
  align-items: center;
  transition: transform 0.3s ease-out;
  justify-content: space-between;
  transform: translateX(0);
  margin-bottom: 93px;
}
.wd-slider--two .wd-slider__slides {
  transform: translateX(-66.66%);
