}
.wd-slider__slide--two .wd-slider__car {
  width: 336px;
}
.wd-slider__cards {
  width: 300%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transform: translateX(-66.66%);
  transition: transform 0.3s ease-out;
  position: absolute;
  bottom: 35px;
  left: 0;
}
.wd-slider--two .wd-slider__cards {
  transform: translateX(0);
}
